'use client';

import { useTheme } from '@/context/ThemeContext';
import { useState, useRef, useEffect } from 'react';
import { SunIcon, MoonIcon } from '@heroicons/react/24/outline';

export default function ThemeToggle() {
	const { theme, toggleTheme } = useTheme();
	const [isOpen, setIsOpen] = useState(false);
	const dropdownRef = useRef<HTMLDivElement>(null);

	// Close dropdown when clicking outside
	useEffect(() => {
		function handleClickOutside(event: MouseEvent) {
			if (
				dropdownRef.current &&
				!dropdownRef.current.contains(event.target as Node)
			) {
				setIsOpen(false);
			}
		}

		document.addEventListener('mousedown', handleClickOutside);
		return () => {
			document.removeEventListener('mousedown', handleClickOutside);
		};
	}, []);

	const themeOptions = [
		{
			value: 'light' as const,
			label: 'Light',
			icon: SunIcon,
		},
		{
			value: 'dark' as const,
			label: 'Dark',
			icon: MoonIcon,
		},
	];

	const currentTheme = themeOptions.find((option) => option.value === theme);

	return (
		<div className='relative' ref={dropdownRef}>
			<button
				onClick={() => setIsOpen(!isOpen)}
				className='flex items-center gap-2 px-3 py-2 text-sm font-medium text-gray-700 bg-white dark:text-gray-200 dark:bg-gray-800 border border-gray-300 dark:border-gray-600 rounded-lg hover:bg-gray-50 dark:hover:bg-gray-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500 transition-colors'
				aria-expanded={isOpen}
				aria-haspopup='listbox'
			>
				{currentTheme && (
					<currentTheme.icon className='w-4 h-4' aria-hidden='true' />
				)}
				<span className='hidden sm:inline'>{currentTheme?.label}</span>
				<svg
					className={`w-4 h-4 transition-transform ${
						isOpen ? 'rotate-180' : ''
					}`}
					fill='none'
					stroke='currentColor'
					viewBox='0 0 24 24'
				>
					<path
						strokeLinecap='round'
						strokeLinejoin='round'
						strokeWidth={2}
						d='M19 9l-7 7-7-7'
					/>
				</svg>
			</button>

			{isOpen && (
				<div className='absolute right-0 mt-1 w-32 bg-white dark:bg-gray-800 border border-gray-200 dark:border-gray-700 rounded-lg shadow-lg z-50'>
					<div className='py-1' role='listbox'>
						{themeOptions.map((option) => (
							<button
								key={option.value}
								onClick={() => {
									toggleTheme();
									setIsOpen(false);
								}}
								className={`flex items-center gap-2 w-full px-3 py-2 text-left text-sm hover:bg-gray-100 dark:hover:bg-gray-700 transition-colors ${
									theme === option.value
										? 'bg-gray-100 dark:bg-gray-700 text-indigo-600 dark:text-indigo-400'
										: 'text-gray-700 dark:text-gray-200'
								}`}
								role='option'
								aria-selected={theme === option.value}
							>
								<option.icon className='w-4 h-4' aria-hidden='true' />
								{option.label}
								{theme === option.value && (
									<svg
										className='w-4 h-4 ml-auto text-indigo-600 dark:text-indigo-400'
										fill='currentColor'
										viewBox='0 0 20 20'
									>
										<path
											fillRule='evenodd'
											d='M16.707 5.293a1 1 0 010 1.414l-8 8a1 1 0 01-1.414 0l-4-4a1 1 0 011.414-1.414L8 12.586l7.293-7.293a1 1 0 011.414 0z'
											clipRule='evenodd'
										/>
									</svg>
								)}
							</button>
						))}
					</div>
				</div>
			)}
		</div>
	);
}
